<template>
    <div id="user">
      <h1>用户管理</h1>
      <table border="1">
         <thead>
             <th>ID</th>
             <th>姓名</th>
             <th>性别</th>
             <th>生日</th>
             <th>操作</th>
         </thead>
         <tbody>
             <tr v-for="user in userList">
               <td>{{user.id}}</td>
               <td>{{user.name}}</td>
               <td>{{user.sex}}</td>
               <td>{{user.birth}}</td>
               <td>
                 <a @click="delUser(user.id)">删除</a>
                 <!--注意如果想要传递参数，并且参数是通过vue的形式获取的，那么就需要通过v-bind绑定-->
                 <a :href="'#/user/userEdit?id='+user.id">修改</a>
               </td>
             </tr>
         </tbody>
      </table><br>
      <a href="#/user/userAdd">添加用户</a>
      <router-view></router-view>

      <Footer></Footer>
    </div>
</template>

<script>
    import Footer from "./Footer";
    export default {
        name: "User",
        components:{       //引入组件
           Footer
        },
        data(){
          return {
            userList:[]
          }
        },
        methods:{
          findAll(){       //查询所有用户的数据
            var _this=this;
            this.$http.get('http://39.100.119.221:8989/vue/user/findAll').then((res)=>{
              console.log(res);
              _this.userList=res.data.results;
            })
          },
          delUser(id){     //根据id删除某个用户，然后再一次查询所有
            var _this=this;
            this.$http.get('http://39.100.119.221:8989/vue/user/delUser?id='+id).then((res)=>{
               if(res.data.msg.code==200){
                 _this.findAll();
               }
            })
          }
        },
        created() {
          this.findAll();
        },
        watch:{        //路由监听，监听来自其它路由的跳转请求
          $route:{
            handler:function (Val,oldVal) {
                 if(Val.path=='/user')   //如果监听到的请求的路径是 '/user'，则说明是添加或者删除请求发来的，重新执行一次查询所有
                   this.findAll();
            }
          }
        }
    }
</script>

<style scoped>

</style>



