</template>

<script>
    export default {
        name: "Student",
        data(){
          return{
            studentList:[
              {id:1,name:"徐健猇",class:"1-10"},
              {id:2,name:"王小二",class:"2-5"},
              {id:3,name:"六i笑傲三",class:"3-6"}
            ]
          }
        }
    }
</script>

