<template>
    <div>
      <h3>用户添加</h3>
      <form>
        姓名：<input type="text" v-model="user.name"><br>
        性别：<input type="text" v-model="user.sex"><br>
        生日：<input type="text" v-model="user.birth">
        <input type="button" value="提交" @click="addUser">
      </form>
    </div>
</template>

<script>
    export default {
        name: "UserAdd",
        data(){
          return {
            user:{}
          }
        },
        methods:{
          addUser(){        //添加一个用户的信息
            this.$http.post('http://39.100.119.221:8989/vue/user/addUser',{
              name:this.user.name,
              sex:this.user.sex,
              birth:this.user.birth
            }).then((res)=>{
                 console.log(res);
                 if(res.data.msg.code==200)
                     this.$router.push('/user');  //切换路由
            })
          }
        }
    }
</script>

<style scoped>

</style>



