<template>
    <div>
      <h3>用户修改</h3>
      ID：<span>{{user.id}}</span><br>
      姓名：<input type="text" v-model="user.name"><br>
      性别：<input type="text" v-model="user.sex"><br>
      生日：<input type="text" v-model="user.birth">
      <input type="button" value="提交" @click="updateCommit">
    </div>
</template>

<script>
    export default {
        name: "UserEdit",
        data(){
          return{
            user:{
              id:""
            }
          }
        },
        methods:{
          findOneById(){    //根据用户的ID查找当前用户信息，并且打印到表单中
              this.$http.get('http://39.100.119.221:8989/vue/user/findOne?id='+this.user.id).then(res=>{
                console.log(res);
                this.user=res.data;
              })
          },
          updateCommit(){   //更新用户数据并且提交,更新成功后跳转到/user路由重新发送一次查询所有
              this.$http.post('http://39.100.119.221:8989/vue/user/updateUser',this.user).then(res=>{
                 if(res.data.msg.code==200)
                    this.$router.push('/user');    //跳转到  /user 路由
              })
          }
        },
        created() {
            //获取待更新用户的ID并且进行设置
           this.user.id=this.$route.query.id;
           this.findOneById();
        }
    }
</script>

<style scoped>

</style>



